<template>
  <div class="pagecontent">
    <h1>{{ h1[$router.app.lang] }}</h1>
    <p>{{ p1[$router.app.lang] }}</p>
    <p>{{ p2[$router.app.lang] }}</p>
    <p>{{ p3[$router.app.lang] }}</p>

    <b-container fluid class="p-4">
      <b-row>
        <b-col md="6" sm="12">
          <b-img fluid src="static/sussieMagne.jpg"/>
        </b-col>
        <b-col md="6" sm="12">
          <b-img fluid src="static/sussieMagneSnorklingEdit.png"/>
        </b-col>
      </b-row>
      <b-row>
        <b-col md="6" sm="12">
          <b-img fluid src="static/sussieMagneBeachEdit.png"/>
        </b-col>
        <b-col md="6" sm="12">
          <b-img fluid src="static/sussieMagneOkinawa.jpg"/>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      h1: {
        SV: 'Nu var det dags!',
        EN: 'It is time!',
        JP: 'やっと決まった！'
      },
      p1: {
        SV: 'Efter 7,5 år tillsammans har Sussie och Magne äntligen bestämt sig för att gifta sig. Frågar ni Sussie vad hon tycker om det svarar hon: “Tänk att vi varit tillsammans i 7,5 år!” (och med det menar hon egentligen att hon har stått ut med Magnes rapar i 7,5 år, och inte blir det lättare nu när de ska bli man och hustru.)',
        EN: 'After 7.5 years Sussie and Magne finally decided to get married. If you ask Sussie how she\'s feeling she\'ll probably answer "can you imagine that we\'ve been together for 7.5 years?" (and by that she means she has endured Magne\'s burping for 7.5 years, and becoming husband and wife is not going to make that easier.)',
        JP: '付き合って7年半経ってからようやく結婚すると決まりました。スッスィーに「感想は？」を聞くと恐らく「付き合って7年半って長いでしょ！」を言うけど、その意味は「7年半こいついつも家帰って、へこいて寝てるばっかやし、これからもっと大変やろう」を示す。と言って半分ジョークやけどｗ'
      },
      p2: {
        SV: 'Skämt åsido, vi ser mycket fram emot ett nytt kapitel i vårt liv och är tacksamma att ni valt att komma och dela denna dag med oss! Fixa frillan och sätt på er dansskorna, så ses vi den 30e juni. :)',
        EN: 'Jokes aside, we are very much looking forward to this new chapter of our lives, and even more so happy that you will share this day with us! Get your groove on and we will see you on the 30th of June! xD',
        JP: 'これからの新生活に迎えるのは嬉しいけど、それよりこの一日を一緒に過ごせる方が嬉しいです！パリピポ以外立入禁止。ダンスフロアまでよろしくどうぞ！'
      },
      p3: {
        SV: 'PS: Ni som ska sova kvar på hotell, kom ihåg att bekräfta er bokning direkt till hotellet senast den 31a maj. Se sidan Boende för mer information.',
        EN: 'PS: Those of you who are staying at the hotel afterwards, please contact the hotel directly to confirm your booking before the 31st of May. See the Accomodation page for more info.',
        JP: 'ホテルに泊まる方々はホテルに連絡しないとならないので、「宿泊」のページを確認ちょうだい。英語は苦手ならメールで連絡してくれればこちらで対応します！'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pagecontent {
  width: 100%;
  float: left;
  padding: 0em 2em 0em 2em;
  position: relative;
}
img {
  padding-top: 0.5em;
}
</style>



